@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;700;900&display=swap);

body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ecf0f1;
  color: #37474F;
}
strong{
  font-weight: 700!important;
}
button{
  outline: none!important;
}
.btn-outline-light:hover{
  background: transparent!important;
}
.bg-info{
  background: #eef5f3!important;
}
.btn-rrss{
  width: 40px;
  height: 40px;
  border-radius: 40px;
  -webkit-border-radius: 40px;
  padding: 0;
  border: 1px solid #999;
}
.btn-rrss i{
  color: #666;
}

.text-success{
  color: #00b894!important;
}
.bg-success{
  background-color: #00b894!important;
}

.login__panel{
  position: relative;
  background-color: #fff;
  overflow: hidden;
}
.login__texto{
  position: relative;
  background: #55efc4; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg,  #55efc4 0%,#6c5ce7 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#55efc4', endColorstr='#6c5ce7',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  overflow: hidden; 
  transition: all 2s ease;
  -webkit-transition: all 2s ease;
  z-index: 2;
}
.login__texto:before{
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://i.pinimg.com/originals/32/8b/51/328b51ff922c7b6ae36329ce9d309187.jpg');
  background-size: cover;
  mix-blend-mode: multiply;
  opacity: .3;
}
@media screen and (min-width: 768px){
    .login__panel{
      height: 475px;
      top: 25px;
      opacity: 0;
      animation: introPanel 1s ease 1s forwards;
    }
    .login__formulario{
      position: relative;
      top: 0;
      left: 45%;
      width: 55%;
      transition: left 2s ease;
      z-index: 1;
    }
    .login__texto{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
    }
    .login__panel.registro .login__texto{
      -webkit-clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
              clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
      animation: animaPath 1s cubic-bezier(.31,.61,.91,.79) forwards;
    }
    .login__texto .login__texto--contenido{
      position: relative;
      display: block;
      margin: 0;
      top: 50%;
      left: 0;
      width: 45%;
      height: auto;
      transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      transition: all 1.5s ease;
    }
    .login__panel.login .login__texto .login__texto--contenido{
      left: 55%;
    }
    .login__panel.login .login__texto{
      -webkit-clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
              clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
      animation: animaLogin 1s cubic-bezier(.31,.61,.91,.79) forwards;
    }
    .login__panel.login .login__formulario{
      left: 0%;
    }
    @keyframes introPanel{
      0%{
        top: 25px;
        opacity: 0;
      }
      100%{
        top: 0;
        opacity: 1;
      }
    }
    @keyframes animaPath{
      0%{
        -webkit-clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
                clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
      }
      50%{
        -webkit-clip-path: polygon(0 0, 70% 0, 70% 100%, 0 100%);
                clip-path: polygon(0 0, 70% 0, 70% 100%, 0 100%)
      }    
      100%{
        -webkit-clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
                clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
      }
    }
    @keyframes animaLogin{
      0%{
        -webkit-clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
                clip-path: polygon(0 0, 45% 0, 45% 100%, 0 100%);
      }
      50%{
        -webkit-clip-path: polygon(0 0, 70% 0, 70% 100%, 0 100%);
                clip-path: polygon(0 0, 70% 0, 70% 100%, 0 100%)
      }
      100%{
        -webkit-clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
                clip-path: polygon(55% 0, 100% 0, 100% 100%, 55% 100%);
      }
    }
}

.top__imagen{
  position: relative;
  float: left;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  -webkit-border-radius: 100%;
}
.top__nombre{
  float: left;
  margin-left: 10px;
  line-height: 35px;
}

.tareas{
  overflow: hidden;
}
.tabs{
  width: 100%;
  height: 60px;
}
.tabs__tab{
  float:left;
  width: 50%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-weight: light;
  font-size: 15px;
  background: #718093;
  cursor: pointer;
}
.tabs__tab.active{
  background: #0d6efd;
}

.listado__tareas{
  margin:0 auto;
  width: 90%;
  padding: 0;
  list-style: none;
}
.tarea{
  position: relative;
  border-bottom: 1px solid #eee;
}
.tarea .fa-chevron-down, .tarea .fa-chevron-up{
  cursor: pointer;
}
.tarea:last-of-type{
  border-bottom: 0;
}
.tarea:after, .tarea__contenido:after, .tarea__contenido--top:after, .tarea__contenido--info:after{
  content: '';
  display: table;
  clear: both;
}
.tarea__contenido{
  height: 75px;
  overflow: hidden;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
}
.tarea.open .tarea__contenido{
  height: 200px;
}
.tarea__contenido--opciones .btn{
  width: 40px;
  height: 40px;
  border-radius: 40px;
  -webkit-border-radius: 40px;
}
@media screen and (min-width: 768px){
  .tarea__contenido{
    height: 40px;
  }
  .tarea.open .tarea__contenido{
    height: 100px;
  }
  .tarea__contenido--info p{
    width: 60%;
  }
  .tarea__contenido--opciones{
    width: 30%;
  }
}

.buscador__tarea, .boton__agregar{
  position: relative;
}
.buscador__tarea i{
  position: absolute;
  display: block;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
}

.boton__agregar i{
  position: absolute;
  display: block;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  z-index: 2;
}
textarea{
  resize: none;
}
.DayPickerInput{
  width: 100%;
}
.fa-calendar-alt{
  position: absolute;
  right: 15px;
  top: 12px;
}
.DayPickerInput input{
  display: block;
  margin-bottom: 1rem!important;
  width: 100%;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: .25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
